@import 'normalize.css';
@font-face {
  font-family: "MyriadPro-Light";
  src: url(../fonts/MyriadPro-Light.eot), url(../fonts/MyriadPro-Light.woff);
}
body {
  background: #fafafa;
  font-family: "MyriadPro-Light", sans-serif;
}
.mainWrapper {
  display: block;
}
header {
  background-color: #fff;
  -webkit-box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  position: relative;
  z-index: 1;
}
.logo {
  width: 30%;
  display: block;
  margin: 20px auto;
}
.logo img {
  vertical-align: bottom;
  display: block;
  width: 100%;
}
.bannerImg img {
  width: 100%;
  display: block;
  vertical-align: bottom;
}
.pageWrapper {
  display: -ms-flexbox;
  display: flex;
  margin: 30px auto;
  width: 90%;
}
.pageWrapper section {
  width: 85%;
}
.pageWrapper aside {
  width: 15%;
  padding-left: 40px;
}
.kinder {
  background: url('../img/kinderfilme.jpg') no-repeat;
  background-size: cover;
  margin-bottom: 20px;
  position: relative;
  display: block;
}
.kinder:before {
  content: '';
  display: block;
  padding-bottom: 100%;
}
.kinder span {
  font-size: 18px;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  position: absolute;
  width: 100%;
  text-align: center;
  left: 0;
  bottom: 0;
  padding: 20px 0;
}
.linkIcon {
  display: block;
  background-color: #333;
  color: #fff;
  text-decoration: none;
  padding: 30px 0;
  margin-bottom: 20px;
  text-align: center;
  font-size: 20px;
}
.linkIcon span {
  text-transform: uppercase;
  display: block;
  margin: 10px 0 0;
}
.linkIcon i {
  display: block;
  background-size: 100% auto;
  background-repeat: no-repeat;
  width: 40%;
  margin: auto;
}
.linkIcon i:before {
  content: '';
  display: block;
  padding-bottom: 95%;
}
.iconCat {
  background-image: url('../img/category_icon.png');
}
.iconForm {
  background-image: url('../img/feedback_icon.png');
}
.listContainer h2 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  border-bottom: 5px solid #333;
  margin: 0 0 30px;
}
.listContainer h2 span {
  color: #fff;
  text-transform: uppercase;
  background-color: #333;
  padding: 10px 20px;
  display: inline-block;
}
.listContainer h2 small {
  float: right;
}
.listContainer ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.listContainer li {
  cursor: pointer;
  color: #333;
  width: 18%;
  margin: 20px 0;
}
.listContainer li figure {
  margin: 0;
}
.listContainer li img {
  width: 100%;
  vertical-align: bottom;
}
.listContainer h3 {
  text-transform: uppercase;
  font-size: 18px;
  margin: 10px 0;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.listContainer p {
  margin: 10px 0 0;
  height: 3.3em;
  overflow: hidden;
  font-size: 13px;
  opacity: 0.6;
}
.formPopup {
  -webkit-animation: fade 0.3s;
  animation: fade 0.3s;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: auto;
  left: 0;
  top: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.9);
  text-align: center;
}
.formPopup:before {
  content: '';
  width: 0;
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}
.formPopup .btnClose {
  color: #fff;
  position: absolute;
  right: 0;
  top: 0;
  background-color: #333;
  font-size: 40px;
  z-index: 1;
  text-decoration: none;
  line-height: 0.9;
  width: 1em;
  height: 1em;
  text-align: center;
  -webkit-transform: translate(50%, -50%);
  -ms-transform: translate(50%, -50%);
  transform: translate(50%, -50%);
}
.formPopup .formBox {
  width: 70%;
  background-color: #fff;
  display: -ms-inline-flexbox;
  display: inline-flex;
  vertical-align: middle;
  position: relative;
  margin: 20px 0;
  text-align: left;
}
.formPopup .formBox > div {
  width: 50%;
  padding: 30px;
}
.formPopup .formBox h2 {
  font-size: 22px;
  font-weight: normal;
  text-transform: uppercase;
  padding: 3px 0;
  border-bottom: 1px solid currentColor;
  margin: 0;
}
.formPopup .formBox img {
  width: 100%;
  margin: 20px 0;
}
.formPopup .formBox p {
  font-size: 14px;
  margin-top: 0;
}
.formPopup .formBox .txtCnt {
  color: #fff;
  background-color: #333;
}
.formPopup .formBox .frmCnt {
  color: #333;
}
.formPopup .frmCnt .formGrp {
  margin: 20px 0 0;
}
.formPopup .frmCnt .formGrp > div {
  margin-bottom: 20px;
}
.formPopup .frmCnt .formGrp > div.half {
  width: 50%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
}
.formPopup .frmCnt .formGrp > div.half:nth-of-type(2) {
  padding-right: 2%;
}
.formPopup .frmCnt .formGrp > div.half:nth-of-type(3) {
  padding-left: 2%;
}
.formPopup .frmCnt .CTA {
  text-align: right;
  margin-bottom: 0 !important;
}
.formPopup .frmCnt button {
  color: #fff;
  text-transform: uppercase;
  background-color: #ef5853;
  border-radius: 0;
  border: none;
  font-size: 20px;
  padding: 0.7em 1.5em;
  cursor: pointer;
}
.formPopup .frmCnt input,
.formPopup .frmCnt select,
.formPopup .frmCnt textarea {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #c1c1c1;
  font-family: inherit;
  font-size: 18px;
  width: 100%;
  padding: 10px;
  border: 0;
}
.formPopup .frmCnt input::-webkit-input-placeholder,
.formPopup .frmCnt select::-webkit-input-placeholder,
.formPopup .frmCnt textarea::-webkit-input-placeholder {
  color: #fff;
  text-transform: uppercase;
}
.formPopup .frmCnt input::-ms-input-placeholder,
.formPopup .frmCnt select::-ms-input-placeholder,
.formPopup .frmCnt textarea::-ms-input-placeholder {
  color: #fff;
  text-transform: uppercase;
}
.formPopup .frmCnt input::placeholder,
.formPopup .frmCnt select::placeholder,
.formPopup .frmCnt textarea::placeholder {
  color: #fff;
  text-transform: uppercase;
}
.smileyBox {
  text-align: center;
  margin: 20px 0;
}
.smileyBox label {
  position: relative;
  width: 2.5em;
  height: 2.5em;
  display: inline-block;
  cursor: pointer;
  margin: 0 3px;
}
.smileyBox label input {
  position: absolute;
  opacity: 0;
}
.smileyBox label i {
  background-image: url('../img/sprite_smiley.png');
  background-repeat: no-repeat;
  background-size: auto 200%;
  width: 100%;
  height: 100%;
  display: block;
}
.smileyBox label[for="sm_1"] i {
  background-position: 100% 0;
}
.smileyBox label[for="sm_1"] input:checked + i {
  background-position: 100% 100%;
}
.smileyBox label[for="sm_2"] i {
  background-position: 75% 0;
}
.smileyBox label[for="sm_2"] input:checked + i {
  background-position: 75% 100%;
}
.smileyBox label[for="sm_3"] i {
  background-position: 50% 0;
}
.smileyBox label[for="sm_3"] input:checked + i {
  background-position: 50% 100%;
}
.smileyBox label[for="sm_4"] i {
  background-position: 25% 0;
}
.smileyBox label[for="sm_4"] input:checked + i {
  background-position: 25% 100%;
}
.smileyBox label[for="sm_5"] i {
  background-position: 0 0;
}
.smileyBox label[for="sm_5"] input:checked + i {
  background-position: 0 100%;
}
.videoPopup {
  -webkit-animation: fade 0.3s;
  animation: fade 0.3s;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: auto;
  left: 0;
  top: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.9);
  text-align: center;
}
.videoPopup:before {
  content: '';
  width: 0;
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}
.videoPopup .btnClose {
  color: #fff;
  position: absolute;
  right: 0;
  top: 0;
  background-color: #333;
  font-size: 40px;
  z-index: 1;
  text-decoration: none;
  line-height: 0.9;
  width: 1em;
  height: 1em;
  text-align: center;
}
.videoPopup > div {
  border: 5px solid #333;
  background-color: #000;
  width: 70%;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  margin: 20px 0;
}
.videoPopup > div:before {
  content: '';
  display: block;
  padding-bottom: 50%;
}
.videoPopup iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 500px) {
  .mainWrapper {
    width: 100%;
  }
  .logo {
    min-width: 320px;
  }
  .pageWrapper {
    display: block;
  }
  .pageWrapper section {
    width: 100%;
  }
  .pageWrapper aside {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    width: 100%;
    padding: 0;
  }
  .pageWrapper aside > a {
    width: 48%;
  }
  .pageWrapper h2 {
    font-size: 18px;
  }
  .listContainer li {
    width: 48%;
  }
  .videoPopup > div {
    width: 97%;
    height: 100%;
    margin: 0;
  }
}



